import React,{Component} from "react"
import {Link,hashHistory} from "react-router"
import {connect} from "react-redux"


import none from "../../../../assets/images/none.png";
import { get_exit } from "../../actions/index";
import   "../../../utils/layer/mobile/layer.js";

@connect(
    // (state)=>({...state})
    state=>state
)
export default class Shezhi extends Component{
    removeStorage=()=>{
        layer.open({
            content: '',
            skin: 'msg',
            time: 2 //2
          });
    }
    render(){
        const {dispatch} = this.props;
        return(
            <div className="shezhi">
               <header>
                   <div className="main">
                    <i className="iconfont icon-fanhui" onClick={()=>{hashHistory.go(-1)}}></i>
                    <h1></h1>
                   </div>
               </header>
               <section>
                   <div className="list">
                       <h3></h3>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <input type="checkbox" /> 
                              
                           </div>
                       </div>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <input type="checkbox" /> 
                              
                           </div>
                       </div>
                       <div className="item">
                           <div className="item-main" onClick={this.removeStorage}>
                               <p></p>
                               <i className="iconfont icon-gengduo"></i>
                           </div>
                       </div>
                   </div>
                   <div className="list">
                       <h3></h3>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <i className="iconfont icon-gengduo"></i>
                              
                           </div>
                       </div>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <i className="iconfont icon-gengduo"></i>
                              
                           </div>
                       </div>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <i className="iconfont icon-gengduo"></i>
                              
                           </div>
                       </div>
                   </div>
                   <div className="list">
                       <h3></h3>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <i className="iconfont icon-gengduo"></i>
                              
                           </div>
                       </div>
                       <div className="item">
                           <div className="item-main">
                               <p></p>
                               <i>4.3.4</i>
                              
                           </div>
                       </div>
                       
                   </div>
                   <div className="list">
                       <h3></h3>
                       <div className="item">
                           <div className="item-main">
                               <p onClick={()=>{dispatch(get_exit(dispatch))}}></p>                           
                           </div>
                       </div>
                      
                   </div>
               </section>
            </div>
        )
    }
}


